import '../css/webflow.css'
import '../css/normalize.css'
import '../css/life-group.webflow.css'

const Contact = () => {  return (
    <div class="section-2 wf-section">
        <div class="container-7 w-container">
            <h2 class="heading-2">Контакты</h2>
            <div class="div-block-10">
                <div>
                    <div>
                        <h4 class="heading-6">Адрес</h4>
                        <div class="text-block-12">Екатеринбург, ул. Шаумяна 87</div>
                    </div>
                    <div class="div-block-12">
                        <h4 class="heading-6">Телефон</h4>
                        <div class="text-block-13">+7 (343) 288-78-53</div>
                    </div>
                    <div class="div-block-11">
                        <h4 class="heading-6">Почта:</h4>
                        <div class="text-block-14">info@uklife.ru</div>
                    </div>
                    </div>
                    <div class="div-block-13">
                    <div class="w-form">
                        <form id="email-form" 
                            name="email-form"
                            data-name="Email Form" 
                            class="form">

                                <input type="text" class="text-field w-input" maxlength="256" name="name-2" data-name="Name 2" placeholder="Имя" id="name-2"></input>
                                <input type="email" class="text-field-2 w-input" maxlength="256" name="email-2" data-name="Email 2" placeholder="Email" id="email-2" required=""></input>
                                <input type="email" class="text-field-2 w-input" maxlength="256" name="Phone-2" data-name="Phone 2" placeholder="Телефон" id="Phone-2" required=""></input>
                                <textarea placeholder="Сообщение" maxlength="5000" id="field-2" name="field-2" data-name="Field 2" class="textarea w-input"></textarea>
                                <input type="submit" value="Отправить" data-wait="Please wait..." class="submit-button w-button"></input>
                        </form>
                        <div class="w-form-done">
                            <div>Thank you! Your submission has been received!</div>
                        </div>
                        <div class="w-form-fail">
                            <div>Oops! Something went wrong while submitting the form.</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
)}

export default Contact;
